@use '../../scss/utils/mixins' as *;

.housingContent {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    height: auto;
    gap: 15px;
    position: relative;

    h1 {
        color: #FF6060;
        font-family: Montserrat;
        font-size: 22px;
        font-weight: 500;
        margin: 0;

        @include desktop {
            font-size: 34px;
        }
    }

    p {
        color: #000;
        font-family: Montserrat;
        font-size: 16px;
        font-weight: 500;
        margin: 0;

        @include desktop {
            font-size: 20px;
        }
    }

    @include tablet {
        border-radius: 25px;
        max-width: 728px;
    }

    @include desktop {
        min-width: 1240px;
    }

    .tagContent {
        display: flex;
        width: 100%;
        height: auto;
        gap: 10px;
    
        .tagSpan {
            display: flex;
            justify-content: center;
            align-items: center;
            width: auto;
            height: 18px;
            padding: 4px 8px;
            border-radius: 5px;
            background: #FF6060;
            color: #FFF;
            text-align: center;
            font-family: Montserrat;
            font-size: 12px;
            font-weight: 700;
        }
    }
    
    .ratingHostContent {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: auto;

        @include desktop {
            display: flex;
            flex-direction: column-reverse;
            align-items: flex-end;
            position: absolute;
            gap: 30px;
        }
    
        .ratingContent {
            display: flex;
            gap: 5px;
    
            .ratingStar {
                max-width: 18px;
                min-height: auto;
            }
        }
    
        .hostContent {
            display: flex;
            align-items: center;
            gap: 10px;
    
            .hostName {
                width: 93px;
                text-align: right;
                color: #FF6060;
                font-family: Montserrat;
                font-size: 16px;
                font-weight: 500;
                margin: 0;

                @include desktop {
                    font-size: 20px;
                }
            }
    
            .hostPicture {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                object-fit: cover;

                @include desktop {
                    width: 60px;
                    height: 60px;
                }
            }
        }
    }

}



